.empty {
  text-align: center;
  margin-top: 50px; }
  .empty h3 {
    color: #ccc; }
    .empty h3 i {
      vertical-align: middle; }
  .empty p {
    color: #ccc; }
    .empty p i {
      color: #fff;
      cursor: pointer;
      vertical-align: middle;
      border: 2px solid #fff;
      border-radius: 50%;
      margin: 0 3px;
      -webkit-transition: -webkit-box-shadow 0.3s;
      transition: -webkit-box-shadow 0.3s;
      -o-transition: box-shadow 0.3s;
      transition: box-shadow 0.3s;
      transition: box-shadow 0.3s, -webkit-box-shadow 0.3s; }
      .empty p i:hover {
        -webkit-box-shadow: 0 0 6px 3px #ffffff85;
                box-shadow: 0 0 6px 3px #ffffff85; }



