.shelf {
  display: grid;
  grid-template-rows: auto auto auto 1fr;
  justify-items: center;
  font-size: 1rem; }
  .shelf .shelfInput form input, .shelf .shelfInput form button {
    border: none;
    padding: 10px;
    font-family: "Source Sans Pro";
    outline: none;
    border: 1px solid #fff;
    margin-top: 8px; }
  .shelf .shelfInput form input {
    border-radius: 5px 0 0 5px; }
  .shelf .shelfInput form button {
    border-radius: 0 5px 5px 0;
    background-color: #508EF5;
    color: #fff; }
  .shelf .shelfList {
    justify-self: stretch; }
    .shelf .shelfList ul {
      padding-left: 0;
      margin: 0;
      display: grid; }
      .shelf .shelfList ul .swipe-enter {
        opacity: 0.01; }
      .shelf .shelfList ul .swipe-enter.swipe-enter-active {
        opacity: 1;
        -webkit-transition: all 300ms ease-in;
        -o-transition: all 300ms ease-in;
        transition: all 300ms ease-in; }
      .shelf .shelfList ul .swipe-exit {
        opacity: 1;
        margin-left: 30px; }
      .shelf .shelfList ul .swipe-exit.swipe-exit-active {
        opacity: 0.1;
        margin-left: 80px;
        -webkit-transition: all 300ms ease-in;
        -o-transition: all 300ms ease-in;
        transition: all 300ms ease-in; }
      .shelf .shelfList ul li {
        width: 80%;
        position: relative;
        list-style-type: none;
        background-color: #fff;
        margin: 10px auto;
        padding: 5px 20px 5px 10px;
        border-radius: 5px;
        -webkit-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        overflow: hidden; }
        .shelf .shelfList ul li a {
          display: block;
          font-size: 16px;
          text-decoration: none;
          color: #0075be; }
        .shelf .shelfList ul li a.url {
          font-size: 12px;
          margin-top: 5px; }
        .shelf .shelfList ul li a.url-unsolved {
          padding: 5px 0; }
        .shelf .shelfList ul li p {
          font-size: 14px;
          color: #000; }
        .shelf .shelfList ul li i {
          position: absolute;
          top: 5px;
          right: -30px;
          color: #ccc;
          vertical-align: middle;
          visibility: hidden;
          margin-left: 5px;
          cursor: pointer;
          -webkit-transition: all 0.3s;
          -o-transition: all 0.3s;
          transition: all 0.3s; }
        .shelf .shelfList ul li:hover {
          -webkit-box-shadow: 0 0 8px 2px #e6e6e6;
                  box-shadow: 0 0 8px 2px #e6e6e6; }
          .shelf .shelfList ul li:hover i {
            visibility: visible;
            right: 5px; }
          .shelf .shelfList ul li:hover i.done {
            color: #1e79e0; }



