.shelfSwitcher {
  margin: 10px auto;
  text-align: center; }
  .shelfSwitcher .switches {
    display: inline-block;
    border: 1px solid #fff;
    border-radius: 3px; }
    .shelfSwitcher .switches a {
      display: inline-block;
      padding: 5px 15px;
      font-size: 16px;
      text-decoration: none;
      color: #000; }
    .shelfSwitcher .switches a.active {
      background-color: #fff;
      color: #000; }



